/*audio player - plyr*/
.plyr--audio {
    margin: 0 auto;
    top: 50% !important;
    width: 100% !important;
}
.plyr__controls .plyr__volume {display: flex;}
.plyr--full-ui input[type=range] {height: 30px;}
.plyr-audio{
    top: 50%;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    width: 50%;
    border-radius: 4px;
    overflow: hidden;
}
.lecture-content {
    position: relative;
    height: 100%;
    
}
.plyr__volume {
    flex: unset;
    position: relative;
}
/*audio player - plyr*/

@media only screen and (min-device-width : 320px) and (max-device-width : 850px) and (orientation : portrait) {
    .nav-header .ask{display: none;}
    .mp3-header .nav-header .lecture-title {
        line-height: 60px;
    }
    .plyr-audio {
        top: 50%;
        position: absolute;
        left: 50% !important;
        -webkit-transform: translate(-50%,-50%) !important;
        transform: translate(-50%,-50%) !important;
        padding: 0;
        width: 80% !important;
    }
}

@media only screen and (max-device-width : 850px) and (orientation : landscape) {
    .plyr-audio {width:60%;left: 50% !important;transform: translate(-50%,-50%) !important;}
    .plyr-audio .plyr__volume input{min-width: 50px;}
}
