/*Assignmnt settings starts here*/

.curriculam-index {
    font-size: 21px;
    color: #fff;
    font-weight: 400;
    padding-left: 30px;
    transition: 0.6s ease font-size;
}

.lecture-holder {
    width: 100%;
    position: relative;
    z-index: 9;
    top: 180px;
    margin-bottom: 60px;
}

.content-toggle-tab {
    border: 1px solid #713ea7;
    border-radius: 55px;
    background: #fff;
    margin: 0 auto;
    width: max-content;
    width: -moz-fit-content;
    overflow: hidden;
}

.content-toggle-tab .tab {
    font-size: 18px;
    color: #444;
    display: inline-block;
    padding: 5px 30px;
    border-right: 1px solid #713ea7;
    margin-left: -3px;
    cursor: pointer;
    user-select: none;
}

.content-toggle-tab .tab:hover {
    background: #713ea7;
    color: #fff;
}

.content-toggle-tab .tab:last-child {
    border-right: none;
}

.context-holder {
    padding: 0px 0px;
    width: 55%;
    margin: 0 auto;
}

.context-holder img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    margin-bottom: 30px;
}

.d-flex {
    display: flex;
}

.context-holder .context {
    font-size: 18px;
    font-weight: 400;
    text-align: justify;
    padding-top: 40px;
    /* margin-bottom: 100px; */
    background: #fff
}
.time-ramining{align-items: center;}
.time-ramining .time{
    color: #e63856;
}

.context p {
    margin: 0px;
    padding-bottom: 20px;
    word-break: break-word;
}

.preview-area .nav-header {
    color: #444;
    z-index: 9;
    width: 100%;
    background: -moz-linear-gradient(top, rgba(255, 255, 255) 10%, rgba(0, 0, 0, 0) 100%);
    background: -webkit-linear-gradient(top, rgba(255, 255, 255) 10%, rgba(0, 0, 0, 0) 100%);
    background: linear-gradient(to bottom, rgba(255, 255, 255) 10%, rgba(0, 0, 0, 0) 100%);
    /*filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#000000',GradientType=0 );*/
    background: -ms-linear-gradient(bottom, #ffffff, #00000000);
}

.preview-area .nav-header .lecture-title {
    color: #444
}

.preview-area .nav-header .curriculam-index {
    color: #444;
    padding-left: 70px
}

.preview-area.full-height {
    height: 100%!important;
}

.lecture-footer {
    display: flex;
    justify-content:space-between;
    align-items: center;
    background: #fff;
    height: 60px;
    width: 100%;
    z-index: 999;
    position: fixed;
    bottom: 0px;
    box-shadow: 0px 0px 10px 0px #777777;
    transition: 0.6s ease width, 0.6s ease opacity, 0.6s ease visibility;
    overflow: hidden
}

.lecture-footer div {
    font-size: 20px;
    line-height: 62px;
    display: inline-flex;
}

.lecture-footer .ask-icon {
    margin-right: 20px;
}

.lecture-footer .ask {
    font-size: 20px;
    padding-right: 5px;
}

.lecture-footer .ask-icon {
    width: 24px;
    height: 24px;
    display: inline-block;
    background: #595959;
    color: #fff;
    text-align: center;
    border-radius: 50%;
    line-height: 25px;
    font-size: 18px;
    font-weight: 600;
    cursor: pointer;
}

.prev-lecture {
    padding-right: 70px;
}

.prev-lecture span {
    cursor: pointer;
}

.prev-lecture span:before {
    content: '';
    background: url(../../../assets/images/chevron-left.svg) no-repeat;
    width: 25px;
    height: 18px;
    display: inline-block;
    cursor: pointer;
    position: relative;
    top: 2px;
}

.next-lecture {
    padding: 0 60px 0 70px;
}

.next-lecture span {
    cursor: pointer;
}

.next-lecture span:after {
    content: '';
    background: url(../../../assets/images/chevron-right.svg) no-repeat;
    width: 25px;
    height: 18px;
    display: inline-block;
    cursor: pointer;
    position: relative;
    top: 2px;
}

.tablinks.active {
    background: #713ea7;
    color: #fff;
}

.instruction-info {
    display: inline-flex;
    justify-content: center;
    padding-bottom: 40px;
    font-size: 15px;
    font-weight: 600;
    width: 100%;
}

.submision-date {
    color: #ff8300;
    font-size: 16px;
}

.preview-area .menu {
    background: url(../../../assets/images/menu-icon-dark.svg);
    background-repeat: no-repeat;
    background-size: 41px;
    overflow: auto;
}

.preview-area .close-icon {
    background: url(../../../assets/images/close-dark.svg);
    background-repeat: no-repeat;
    background-size: 34px;
}

.attachment-tag {
    font-size: 14px;
    color: #713ea7;
    border: 1px solid #713ea7;
    border-radius: 24px;
    padding: 6px 14px;
    margin-right: 7px;
    cursor: pointer;
    display: inline-block;
    margin: 3px 3px;
}

.redactor-file-item {
    border: 1px solid rgba(114, 72, 167, 1) !important;
    margin-right: 7px !important;
}

.redactor-file-item a {
    font-size: 14px !important;
    color: #713ea7 !important;
}

.attachment-holder {
    padding: 20px 0px;
}

.btn-violet {
    font-size: 16px;
    color: #fff;
    background: #713ea7;
    padding: 10px 50px;
    outline: none;
    border: none;
    border-radius: 3px;
}

.btn-container {
    padding: 25px 0;
}

.tabcontent {
    -webkit-animation: fadeEffect 0.6s;
    animation: fadeEffect 0.6s;
}

.result-wrapper {
    display: flex;
    justify-content: center;
    padding: 20px 0px;
    padding: 20px 0 40px 0;
}

.result-wrapper .result-info {
    text-align: center;
    width: 120px;
}

.result-info-title {
    display: block;
    line-height: 0px;
    font-size: 13px;
    font-weight: 600;
}

.result-info-title:first-child {
    display: inline-block;
    line-height: 37px;
}

.result-wrapper .marks {
    font-size: 32px;
    font-weight: 600;
    color: #02a5f0;
}

.result-wrapper .grade {
    font-size: 32px;
    font-weight: 600;
    color: #24dd00;
}

.result-wrapper .submition-date {
    display: block;
    font-size: 13px;
    font-weight: 600;
    color: #ff8300;
    line-height: 10px;
}

.preview-area .menu {
    position: fixed;
    z-index: 9;
}

.preview-area .lecture-title {
    padding-left: 70px;
}

/*document settings*/

.context-holder.document-context {
    width: 100%;
    padding: 0px 100px;
}

.screen-toggle{padding: 15px 25px 15px 20px;cursor: pointer;}
.screen-toggle .expand-icon{width:26px;}
/* Fade in tabs */

/* .float-ui{display: none;} */

@-webkit-keyframes fadeEffect {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeEffect {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}


/*Assignmnt settings ends here*/
@media (max-width:992px){
    .redactor-box{max-height: 180px; overflow: hidden;}
}
@media screen and (min-width:992px) {
    /* scrollable tile goes here */
    .scrollable-title-wrapper {
        position: absolute;
        width: 100%;
        z-index: 9;
        padding: 26px 40px 0px 100px;
    }
    .scrollable-title-wrapper .content-title {
        overflow: hidden;
        font-size: 40px;
        font-weight: 300;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }
    .scrollable-title-wrapper .content-title-index {
        font-size: 21px;
        font-weight: 400;
    }
    .scrollable-title-wrapper .content-title-index .section-count {
        padding-right: 15px;
    }
    .content-toggle-tab .tab {
        font-size: 14px;
    }
    /* scrollable tile ends here */

}
