/* floating option starts here */
  .float-ui{
    background: #000000aa;
    box-shadow: 0 0 10px 0 #777;
    width: 100%;
    margin: 0 auto;
    position: fixed;
    bottom: 0px;
    left: 0px;
    right: 0px;
    top: 0px;
    z-index: 9;
    opacity:0;
    transition: opacity 0.6s ease, visibility 0.6s ease;
    visibility: hidden;
  }
  .float-ui.active{opacity:1;visibility: visible;}
  
  .float-ui ul{
    list-style: none;
    padding: 0px;
    margin: 0px;
    background: #fff;
    position: fixed;
    left: 10px;
    right: 10px;
    bottom: 10px;
    border-radius: 10px;
    transform: translateY(100%);
    transition: 0.2s ease transform;
    
  }
  .float-ui.active ul{transform: translateY(0%);}
  .float-ui ul li{
    padding: 15px 15px;
    border-bottom: 1px solid #e0e0e0;
    text-align: center;
    font-size: 16px;
    margin-left: 10px;
    margin-right: 10px;
    list-style: none !important;
  }
  .float-ui ul li:last-child{border:none}
  .toggle-docs{padding:10px;}
  .lecture-footer-container .ask{display:none;}
  .screen-toggle .expand-icon {width: 20px;}


