/*Curriculam starts here*/
.curriculam-wrapper{
   position: fixed;
  left: 0;
  width: 35%;
  height: 100%;
  background: #fff;
  transform: translateX(-100%);
  transition: 0.6s ease transform;
}
.curriculam-head{
  background: #713ea7;
  padding: 15px 20px;
  box-shadow: 0px 6px 4px -4px #ccc;
  position: relative;
}

.curriculam-head .close-icon{
width: 25px;
height: 22px;
cursor: pointer;
background: url(../../../assets/images/close.svg);
background-size: 22px;
background-repeat: no-repeat;
display: inline-block;
position: absolute;
right: 15px;
top: 24px;
}

.curriculam-head-title{
 font-size: 22px;
  font-weight: 400;
  color: #fff;
  display: inline-block;
  width: 250px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
/*Curriculam ends here*/


/* Curricculam content settings starts here */
.info-bold{font-weight: 600 !important; font-size: 15px !important;}
.curriculam-container{
background: #ffffff;
height: calc(100% - 65px);
overflow-y: auto;
border-right: 1px solid #d6d8da;
}
.curriculam-container::-webkit-scrollbar {width: 10px;height: 60px;}
.curriculam-container::-webkit-scrollbar-track {background: #f1f1f1;}
/* Handle */
.curriculam-container::-webkit-scrollbar-thumb {background: #afafaf;border-radius:12px;height: 60px;}
/* Handle on hover */
.curriculam-container::-webkit-scrollbar-thumb:hover {background: #713ea7;}
.milestone-holder{
float: left;
position: absolute;
z-index: 99;
display: inline-block;
left: 25px;
height: 100%;
}
.milestone{
display: block;
width: 25px;
height: 25px;
font-size: 22px;
background: #fff;
position: relative;
left: -1px;
z-index: 9999;
border-radius: 100%;
border: 2px solid #babec2;
box-sizing: inherit;
transform: translate(0,-50%);
top: 50%;
}
.tail-up{
background: rgb(186, 190, 194);
left: 10px;
top: 0;
bottom: 50%;
position: absolute;
width: 2px;
}
.tail-down{
background: rgb(186, 190, 194);
left: 10px;
top: 50%;
bottom: 0%;
position: absolute;
width: 2px;
}
.timeline-text{
padding: 0 15px;
overflow: hidden;
width: 100%;
vertical-align: top;
display: flex;
justify-content: space-between;
cursor: pointer;
}
.curriculam-title {
padding: 5px 20px 20px 20px;
cursor: pointer;
background: #fff;
box-shadow: 0px 2px 3px 0px #e0e0e0;
margin-top: 15px;
}
/*.curriculam-title:hover{background: #f9f9f9;}*/
.curriculam-title h4{    
font-size: 22px;
font-weight: 400;
font-family: 'Open Sans', sans-serif;
margin: 0;
color:#64277d;
}
.timeline-text .curriculam-info-text {
padding: 18px 25px 18px 45px;
margin-bottom: 0;
width: 87%;
font-size: 16px;
font-weight: 400;
font-family: 'Open Sans', sans-serif;
color: #000;
/*  white-space: nowrap;
overflow: hidden;
text-overflow: ellipsis;*/
}

.timeline-row{
background: #fff;
border-bottom: 1px solid #D6D8DA;
box-sizing: border-box;
display: block;
vertical-align: top;
width: 100%;
border-top: 1px solid #D6D8DA;
margin-top: -1px;
position: relative;
}
.timeline-row:hover{background: #f5f5f5;}
.timeline-row a{display: block;text-decoration: none;}

