.modal-container{
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4);
    display: none;
}
.modal-container.visible{display: block;}
.modal-container.curropen{left: 35%; width: 65%; transition: left 0.6s ease, width 0.6s ease;}
.modal-container.forumopen{left: 0; width: 65%; transition: left 0.6s ease, width 0.6s ease;}
.modal-wrapper{
    max-width: 500px;
    width: 450px;
    height: 300px;
    background-color: #fff;
    margin: 0 auto;
    border-radius: 4px;
    margin-top: 60px; 
}
.modal-wrapper.visible{display: block;}
.popup-content{padding: 15px;}
.popup-content h2{
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    color: #444;
    padding: 50px 0px;
    max-height: 180px;
    overflow: auto;
}
.popup-content h3{
    text-align: center;
    color: #444;
    padding: 20px 0px;
}
.popup-content .form-group{
    display: flex;
    flex-direction: column;
    margin: 20px
}
.popup-content .form-group label{
    flex: 1;
    margin-bottom: 5px;
    font-weight: bold;
}
.popup-content .form-group input{
    flex: 1;
    padding: 5px;
    border: 1px solid #aaa;
    border-radius: 3px;
}
.popup-content .form-group p{
    flex: 1;
    margin-top: 10px;
}
.confirm-btn-wrapper{text-align: center;padding: 10px 0px 0px 0px;}
.pop-btn-cancel{
    color: #fff;
    background-color: #d9534f;
    border-color: #d43f3a;
    cursor: pointer;
    display: inline-block;
    font-weight: 400;
    text-align: center;
    user-select: none;
    border: 1px solid transparent;
    padding: 5px 18px;
    font-size: 16px;
    line-height: 1.5;
    border-radius: 4px;
    margin: 0px 5px;
}
.pop-btn-ok{
    color: #fff;
    background-color: #46a53f;
    border-color: #46a53f;
    cursor: pointer;
    display: inline-block;
    font-weight: 400;
    text-align: center;
    user-select: none;
    border: 1px solid transparent;
    padding: 5px 18px;
    font-size: 16px;
    line-height: 1.5;
    border-radius: 4px;
    margin: 0px 5px;
}
.pop-btn-cancel,.pop-btn-ok:focus{outline: none;}
@media (max-width:480px){
    .modal-wrapper {max-width: 95%;}
}


@media only screen and (max-device-width : 850px) and (orientation : landscape) {
    .modal-container {padding-top: 0px;}
    .modal-wrapper{margin-top: 20px;}
}