.faculty-title{
    display: inline-block;
    font-size: 16px;
    font-weight: 500;
    color: #7248a7;
    padding: 5px 15px;
    cursor: pointer;
}

.faculty-feedback{
    text-align: left;
    margin: 8px 0px;
    font-size: 17px;
    font-weight: 600;
    text-transform: capitalize;
}

.faculty-feedback h4{
    margin: 8px 0px;
    color: #7248a7;
    font-weight: 600;
}

.faculty-feedback p{
    font-weight: 500;
}

.content-title,.content-title-index{transition: 0.6s ease font-size;}
.curriculam-toggle .scrollable-title-wrapper .content-title {font-size: 28px;font-weight: 300;}
.curriculam-toggle .scrollable-title-wrapper .content-title-index{font-size: 18px;}
.hidden{display: none}

@media only screen and (min-device-width : 320px) and (max-device-width : 850px) and (orientation : portrait) {
    .instruction-info{padding: 15px 0px;}
}