

.vimeo iframe {
    width: 100%;
    /* height: 100%; */
    position: absolute;
}


@media only screen and (min-device-width : 320px) and (max-device-width : 850px) and (orientation : portrait) {
    .nav-header.dark-header{
        background: #000 !important;
    }
    .nav-header .lecture-title {
        font-size: 19px;
        font-weight: 600;
        line-height: 50px;
        padding-left: 10px;
        padding-top: 0px;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    /* .youtube_video iframe{height:calc(100% - 40px)} */
}

