    transition: 0.6s ease font-size;
}

.lecture-holder {
    width: 100%;
    position: relative;
    z-index: 9;
    top: 180px;
    margin-bottom: 60px;
}

.content-toggle-tab {
    border: 1px solid #713ea7;
    border-radius: 55px;
    background: #fff;
    margin: 0 auto;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    width: -moz-fit-content;
    overflow: hidden;
}

.content-toggle-tab .tab {
    font-size: 18px;
    color: #444;
    display: inline-block;
    padding: 5px 30px;
    border-right: 1px solid #713ea7;
    margin-left: -3px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.content-toggle-tab .tab:hover {
    background: #713ea7;
    color: #fff;
}

.content-toggle-tab .tab:last-child {
    border-right: none;
}

.context-holder {
    padding: 0px 0px;
    width: 55%;
    margin: 0 auto;
}

.context-holder img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    margin-bottom: 30px;
}

.d-flex {
    display: flex;
}

.context-holder .context {
    font-size: 18px;
