/*audio player - plyr*/
.plyr--audio {
    margin: 0 auto;
    top: 50% !important;
    width: 100% !important;
}
.plyr__controls .plyr__volume {display: flex;}
.plyr--full-ui input[type=range] {height: 30px;}
.plyr-audio{
    top: 50%;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    width: 50%;
    border-radius: 4px;
    overflow: hidden;
}
.lecture-content {
    position: relative;
    height: 100%;
    
}
.plyr__volume {
    flex: unset;
    position: relative;
}
/*audio player - plyr*/

@media only screen and (min-device-width : 320px) and (max-device-width : 850px) and (orientation : portrait) {
    .nav-header .ask{display: none;}
    .mp3-header .nav-header .lecture-title {
        line-height: 60px;
    }
    .plyr-audio {
        top: 50%;
        position: absolute;
        left: 50% !important;
        -webkit-transform: translate(-50%,-50%) !important;
        transform: translate(-50%,-50%) !important;
        padding: 0;
        width: 80% !important;
    }
}

@media only screen and (max-device-width : 850px) and (orientation : landscape) {
    .plyr-audio {width:60%;left: 50% !important;-webkit-transform: translate(-50%,-50%) !important;transform: translate(-50%,-50%) !important;}
    .plyr-audio .plyr__volume input{min-width: 50px;}
}

.scorm-header .lecture-title {color: #444;}
.curriculam-toggle .scorm-header .lecture-title {color: #444;}

.scorm-header .nav-header .close-icon {
    background: url(../../static/media/close-dark.1ad17ea0.svg);
    background-repeat: no-repeat;
    background-size: 34px;
}
.scorm-header .menu {
    background: url(../../static/media/menu-icon-dark.f0e7a700.svg);
    background-repeat: no-repeat;
    background-size: 41px;
    overflow: auto;
}
.scorm-header .curriculam-index{color: #444;}
.locked-title.scorm-title{padding: 15px 0px;}

@media only screen and (max-device-width: 850px) and (min-device-width: 320px) and (orientation: portrait){
    .scorm-header .nav-header .menu {
        background-size: 30px !important;
        width: 30px;
        height: 24px;
    }
    .scorm-header .nav-header .lecture-title {line-height: 60px;}
    .lecture-holder.scorm-holder{padding: 60px 15px 0px 15px;}
    .lecture-holder {top: 60px;}
    .scorm-header .nav-header{box-shadow: 0px 0px 10px 0px #777777;}
    .scorm-info{font-size: 16px;}
    /* option icon settings here */
      .scorm-header .nav-header .close-icon {
        background: url(../../static/media/option-icon-dark.da4f573c.svg);
        background-repeat: no-repeat;
        background-size: 25px;
